@import 'normalize.scss';
@import 'variables.scss';
@import 'fonts.scss';
@import 'utils.scss';
@import 'grid.scss';
@import 'header.scss';
@import 'main.scss';
@import 'newsletter.scss';
@import 'footer.scss';
@import 'poligonos.scss';
@import 'carousel.scss';

.carousel {

    .container {

        .carousel-item {
            margin-top: 30px;
            margin-bottom: 30px;
            height: auto;
            width: 200px;


            .img-instagram {
                width: 100%;
                height: 100px;
            }

        }

        #carouselExampleIndicators {

            .carousel-indicators li {
                height: 10px;
                width: 10px;
                background-color: #ED282D;

                &.active {
                    background-color: #ED282D;
                }

                .carousel-control-prev {
                    width: 50px;
                    height: 50px;
                    color: #ED282D;
                    bottom: 0px !important;
                    top: auto;
                    left: 0px;
                }

                .carousel-control-next {
                    width: 50px;
                    height: 50px;
                    color: #ED282D;
                    bottom: 0px !important;
                    top: auto;
                    right: 0px;
                }
            }
        }
    }
}